import './Contact.css';
import { FaRegPaperPlane } from "react-icons/fa";


export default function Bio () { 


  function formReset() {
        document.getElementById('comment').value='';
        document.getElementById('name').value='';
        document.getElementById('email').value='';
    }

   function delayedReset() {
        window.setTimeout(formReset, 2000);
      }


    return (
        <div className="Contact-form">
            <form action="https://formsubmit.io/send/mattlmorrison1@gmail.com" method="POST" target="_blank">
                <div className="Field-input">
                    <div className="Text-align">
                        <p>Name: </p>
                    </div>
                    <div className="Input-align">
                        <input className="Name-input" placeholder="Enter name" name="name" id="name"></input>
                    </div>
                </div>
                <div className="Field-input">
                    <div className="Text-align">
                        <p>Email: </p>
                    </div>
                    <div className="Input-align">
                        <input className="Email-input" placeholder="Enter email" name="email" id="email"></input>
                    </div>
                </div>
                <div className="Field-input">
                    <div className="Text-align">
                        <p>Message: </p>
                    </div>
                    <div className="Message-align">
                        <textarea type="text" placeholder="Enter message" name="comment" id="comment"></textarea>
                    </div>
                </div>
                <div>
                    <div className="Send"> <button type="submit" onClick={delayedReset}>Send <div className="Share"><FaRegPaperPlane /></div></button></div>
                </div>
            </form>
        </div>
    )
}
